.home_box {
  width: 100vw;
  // margin-top: -50px;
  color: #ccc;
  background-color: #25282b;
  font-size: 0.2rem;
  margin-bottom: -40px;
  font-weight: 300;

  //   .px1200{
  //     padding: 0 calc(50vw - 600px);
  //   }
  //   .Body-link {
  //     color: #246cad;
  //   }
  .H2 {
    font-size: 0.32rem;
    color: #d4e05c;
    letter-spacing: 2px;
    font-weight: 300;
    font-family: Tachyon, sans-serif;
  }

  .H3 {
    font-family: Termina, sans-serif;
    font-weight: 600;
    font-size: 0.32rem;
  }

  .index_part1 {
    // margin-top: 6.15764vh;
    height: calc(100vh - 0.5rem);
    width: 100vw;
    overflow: hidden;
    position: relative;
    // canvas {
    //   height: 100%;
    // }

    img,
    .index_part1-1 {
      // height: 100vh;
      position: absolute;
      top:calc(100vh - 320px);
      left: 10px;
    }

    .index_part1-1 {
      font-family: Tachyon, sans-serif;
      letter-spacing: 4px;
      font-weight: 400;
      padding: 0 2.66667vw;
      box-sizing: border-box;

      .index_part1-2 {
        margin-top: 8.62069vh;
        font-size: 0.36rem;
        position: relative;
        color: #a9a9a9;
      }

      .index_part1-3 {
        font-size: 11.82266vh;
        line-height: calc(10.81633vh - 8.65306px);
        position: relative;
        color: #fff;
        font-size: 0.25rem;
      }
      .describe{
        font-family: ArialMT, sans-serif;
        letter-spacing: 2px;
        font-size: 0.18rem;
        margin-top: -5px;
        span {
          display: inline-block;
          border-bottom: 1px solid #fff;
          cursor: pointer;
          margin-top: 5px;
          a{
            color: #fff;
          }
          // text-shadow: 2px 0px 0px rgba(255, 255, 255, .5);
        }
        .moblie{
          margin-left: 25px;
        }
        .no-d{
          border-bottom:1px solid transparent;
        }
      }

      .index_part1-4 {
        position: relative;
        font-size: 3.94089vh;
        line-height: calc(8.36735vh - 6.69388px);
        color: #fff;
      }
    }
  }

  // .index_part2 {
  //   line-height: 3.32512vh;
  //   font-size: 2.46305vh;
  //   z-index: 1;
  //   position: relative;
  //   top: -60vh;
  //   padding: 0 2.66667vw;

  //   .index_part2-center {
  //     height: 100%;
  //     font-weight: 300;

  //   }
  // }

  .index_part2 {
    // margin-top: 0.1rem;
    .index_part2-center {
      height: 100%;
      font-weight: 300;
      flex-wrap: wrap;
      padding: 0.1rem;
      display: flex;
      .stats-box {
        width: 45rem;
        height: 1.76rem;
        background-color: #0d0e0f;
        font-family: Arial MT, sans-serif;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: rgba(13, 14, 15, 0.8);
        border-radius: 10px;
        box-shadow: 6px 6px 8px rgb(31 31 31 / 70%);
        margin-top: 0.1rem;
        .data {
          color: #d4e05c;
          font-size: 0.32rem;
          font-family: led calculator;
        }
        .label {
          font-size: 0.2rem;
          margin-top: 0.04rem;
        }
      }
    }
  }

  .index_part3 {
    padding: 0 2.66667vw;
    // margin-top: -10vh;

    .top_pater {
      .label {
        margin: 5vh 0;
      }

      .line_of_box {
        // position: relative;
        height: 100%;

        .box {
          background-color: rgba(13, 14, 15, .8);
          border-radius: 1.23153vh;
          margin-bottom: 4.92611vh;

          .box_top {
            width: 94.66667vw;
            height: 46.79803vh;
            position: absolute;
            padding: 0 4vw;
            z-index: 1;
            border-radius: 1.23153vh;
            box-sizing: border-box;

            img {
              position: relative;
              width: 6.15764vh;
              height: 6.15764vh;

            }
          }

          img {
            position: relative;
            width: 43.71921vh;
            height: 36.57635vh;
            margin: 4.11084vh calc(47.33333vw - 21.85961vh);
          }

        }

      }
    }
  }

  .index_part4 {
    padding: 0 2.66667vw;

    .top_pater {
      .label {
        margin-bottom: 5vh;
      }

      .line_of_box {
        height: 100%;

        .box {
          background-color: rgba(13, 14, 15, .8);
          border-radius: 1.23153vh;
          margin-bottom: 4.92611vh;

          .box_top {
            width: 94.66667vw;
            height: 46.79803vh;
            padding: 0 4vw;
            z-index: 2;
            border-radius: 1.23153vh;
            box-sizing: border-box;

            img {
              position: relative;
              width: 6.15764vh;
              height: 6.15764vh;
              margin: 4.11084vh calc(47.33333vw - 21.85961vh);
            }

            .box_lable {
              color: #fff;
            }
          }
        }
      }
    }
  }

  .index_part5,
  .index_part6 {
    .label {
      padding: 0 4vw;
      padding-bottom: 4.92611vh;
    }

    .pic_inline {
      .pic {
        width: 23.09113vh;
        display: inline-block;
        // margin: h;
        height: 16.28571vh;
        padding: 0 0 2.46305vh;
        background-color: #25282b;

        img {
          width: 20.62808vh;
          height: 16.28571vh;
          margin: 0 2.66667vw;
        }
      }

    }
  }

  .m-footer {
    height: 12.31527vh;
    bottom: 0px;
    padding: 2.58621vh 2.58621vh 0;
    background-color: #25282b;
    border-top: 1px solid #707070;
    box-sizing: border-box;

    .wechat {
      background-color: white;
      background-image: url('../../../assets/img/wechat.jpeg');
      background-size: cover;
      border: 1px solid #707070;
      width: 99px;
      height: 99px;
      border-radius: 10px;
      display: none;
      position: absolute;
      top: -125px;
      left: -10px;

      .down_box {
        border-right: 8px solid transparent;
        border-left: 8px solid transparent;
        border-bottom: 10px solid white;
        position: absolute;
        transform: rotate(180deg);
        bottom: -10px;
        right: 67px;
      }
    }

    .footer-link {
      .fa {
        position: relative;
        color: #2f2f2f;
        font-size: 2.46305vh;
      }

      .circle {
        position: relative;
        display: flex;
        float: left;
        background-color: #666666;
        width: 3.69458vh;
        height: 3.69458vh;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        margin-right: 20px;
      }

      .circle:hover .wechat {
        display: block
      }
    }

    .no-p {
      padding-left: 0;
    }

    .banquan {
      padding-top: 1.84729vh;
      clear: left;
      font-family: ArialMT, sans-serif;
      font-weight: 400;
      font-size: 1.84729vh;
      color: rgba(255, 255, 255, 0.5);
      margin-bottom: 8px;

    }
  }
}
.zh{
  .home_box .index_part1 .index_part1-1 .describe .moblie{
    margin-left: 0px;
  }
}